import HomeReducer, { initialState, actionCreators } from '../index';

describe('store - index', () => {
  describe('actions', () => {
    it('check create actions', () => {
      const expectedActions = [{ type: 'home/CHANGE_TEXT', text: '' }];
      const actions = [actionCreators.changeText()];
      expect(actions).toEqual(expectedActions);
    });
  });

  describe('reduce', () => {
    let state = HomeReducer(undefined, {});
    it('should return the initialState', () => {
      expect(state).toEqual(initialState);
    });

    it('change text to "hi"', () => {
      state = HomeReducer(state, actionCreators.changeText('hi'));
      expect(state).toHaveProperty('text', 'hi');
    });
  });
});
